.work_hero {
  width: 100%;
  height: 100vh;
}
.container-1 {
  width: 60%;
  text-align: center;
  position: relative;
  left: 20.5%;
  top: 41%;
}
#work_heading {
  font-size: 57px;
  line-height: 70px;
  word-spacing: 0.03em;
  letter-spacing: 0.01em;
  color: #d6d6d6;
}
.clients {
  width: 58%;
  margin: 50px 21%;
}
.work-section {
  display: flex;
  gap: 4em;
  width: 90.3%;
  overflow-x: hidden;
  margin-left: 9.7%;
}
.work-container {
  margin-bottom: 70px;
  border-radius: 10px;
}
.work-div {
  border-radius: 18px;
  width: 333px;
  height: 73vh;
  padding: 0px 10px 0px 33px;
  background-color: #111111;
}
.bg1 {
  background-image: url("./images/img1.png");
  border-radius: 20px;
  background-size: cover;
}
.bg2 {
  background-image: url("./images/Mask\ group\ 1.png");
  border-radius: 20px;
  background-size: cover;
}
.bg3 {
  background-image: url("./images/img2.png");
  border-radius: 20px;
  background-size: cover;
}
.bg4 {
  background-image: url("./images/image\ 17\ \(2\).png");
  border-radius: 20px;
  background-size: cover;
}
.bg5 {
  background-image: url("./images/image\ 17.png");
  border-radius: 20px;
  background-size: cover;
}
.bg6 {
  background-image: url("./images/image\ 17\ \(1\).png");
  border-radius: 20px;
  background-size: cover;
}
.bg7 {
  background-image: url("./images/image\ 17\ \(3\).png");
  border-radius: 20px;
  background-size: cover;
}
.bg8 {
  background-image: url("./images/image\ 17\ \(4\).png");
  border-radius: 20px;
  background-size: cover;
}
.work-section-h1 {
  font-size: 38px;
  line-height: 70px;
  color: #e5e5e5;
  margin: 40px 0;
  padding-top: 50px;
}
.work-section-para {
  font-family: graphik;
  font-size: 15px;
  color: #8c8c8c;
  line-height: 23px;
  margin: 60px 0px;
  width: 92%;
}
.work-btn {
  font-size: 16px;
  margin: 5px;
  font-weight: 700;
  border: 1px solid;
  border-bottom: 1px solid gray;
  border-top: 1px solid gray;
  width: 50%;
  padding: 5px;
  border-radius: 20px;
  transition: 0.5s;
  text-align: center;
}
.expmore {
  font-size: 50px;
  width: 132%;
  height: 70vh;
  display: flex;
  justify-content: center;
  background-color: #111111;
  align-items: center;
}
.invisible {
}
@media screen and (max-width: 1440px) {
  #work_heading {
    font-size: 50px;
    line-height: 65px;
  }
}
@media screen and (max-width: 1024px) {
  #work_heading {
    font-size: 40px;
    line-height: 55px;
  }
}
@media screen and (max-width: 768px) {
  #work_heading {
    font-size: 35px;
    line-height: 45px;
  }
  .clients {
    width: 80%;
    margin: 50px 7.5%;
  }
  .work-section {
    gap: 2em;
    flex-direction: column;
    width: 90%;
    margin-left: 5%;
  }
  .work-div {
    border-radius: 18px;
    width: 100%;
    height: 73vh;
    margin: 0;
    margin-left: 0%;
    background-color: #111111;
    padding-left: 2.5% ;
  }
  .expmore {
  font-size: 40px;
  width: 100%;
}
}
@media screen and (max-width: 600px) {
  .container-1 {
    width: 90%;
    left: 5%;
  }
  #work_heading {
    font-size: 30px;
    line-height: 40px;
  }
  .work-section-h1 {
    font-size: 28px;
    line-height: 40px;
  }
  .work-section-para {
    font-size: 12px;
    color: #8c8c8c;
    line-height: 18px;
    width: 85%;
  }
}
