.empathy-hero{
    text-align: center;
    width: 100%;
    height: 100vh;
    margin-top: 17%;
}
.empathy-content{
    width: 55%;
    margin-left: 23%;
    margin-top: 13%;
}
.date{
    font-family: graphik;
    color: #5a5a5a;
    margin-right: 27%;
}
.empathy-hero-h1{
    font-size: 80px;
    color: #bebebe;
}
.cheese-img{
    width: 2.5%;
    position: relative;
    right: 45px;
}
.cheese{
    margin-left: 60px;
    margin-top: 50px;
    font-family: graphik;
    font-size: 27px;
    color: #656565;
    display: flex;
    justify-content: center;
    align-items: center;
}
.empathy{
    width: 55%;
    margin: 43px;
}
.empathy-para{
    font-family: graphik;
    margin-top:35px ;
    line-height: 34px;
    color: #b9b9b9;
}
.empathy-h1{
    font-size: 60px;
    text-align: center;
    margin: 60px 0px;
    color: #d5d4d4;
}
@media screen and (max-width:1000px){
    .empathy-hero{
        height: auto;
    }
    .empathy-hero-h1{
        font-size: 60px;
    }
    .empathy-h1{
        font-size: 40px;
    }
    .empathy-para{
        margin-top:25px ;
        line-height: 28px;
        color: #b9b9b9;
    }
    .empathy-content{
        width: 70%;
        margin-left: 15%;
    }
    .cheese-img{
        width: 5%;
    }
}
@media screen and (max-width:700px){
    .empathy-content{
        width: 90%;
        margin-left: 5%;
        margin-top: 8%;
    }
    .empathy-hero-h1{
        font-size: 40px;
    }
    .empathy{
        width: 80%;
        margin: 43px 0px 43px 5%;
    }
    .empathy-para{
        margin-top:20px ;
        line-height: 18px;
        font-size: 12px;
        text-align: justify;
    }
    .empathy-h1{
        font-size: 30px;
        margin: 40px 0px;
    }
}