.career-hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 80vh;
}
.career-hero-h1 {
  font-size: 56px;
  width: 60%;
  margin-left: 9.7%;
  margin-top: 17%;
  color:#E4E4E4;
  word-spacing: 0.05em;
  letter-spacing: 0.02em;
}
.career-hero-para {
  font-size: 15px;
  width: 55%;
  color: #7e7e7e;
  font-family: graphik;
  line-height: 20px;
  margin-left: 10%;
}
.whyus {
  height: 100vh;
}
.whyus-h1 {
  font-size: 75px;
  margin: 12% 0px 20px 10%;
}
.whyus-para {
  font-size: 15;
  font-family: graphik;
  color: #7e7e7e;
  width: 63%;
  line-height: 23px;
  margin-left: 10%;
}
.whyus-container {
  width: 18%;
  height: 45vh;
  border-radius: 30px;
  margin-left: 30px;
  margin-top: 12vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  background-color: #111111;
}
.main-container {
  display: flex;
  justify-content: center;
}
.wucontainer-para {
  font-size: 15px;
  padding: 15px;
  color: #848484;
  font-family: graphik;
}
.card-imgs{
  width: 40%;
  position: relative;
  left: 28%;
  top: 18%;
}
.no {
  font-size: 26px;
  font-family: graphik;
  margin-top: 26px;
}
.life-container {
  width: 40%;
  margin-left: 9.7%;
}
.life-h1 {
  font-size: 75px;
  margin-bottom: 25px;
}
.life-para {
  font-size: 15px;
  font-family: graphik;
  color: #7e7e7e;
  line-height: 23px;
}
.horizontal-slide{
  display: flex;
  width: 105%;
  gap: 5em;
  overflow-x: hidden;
  position: relative;
  left: -5%;
  margin:5% 0;
}
.HS-img{
  width: 60%;
  margin-bottom: 100px;
}
.positions {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  width: 58%;
  margin-left: 11%;
  font-size: 22px;
  transition: 0.3s;
  position: relative;
  cursor: pointer;
}
.position-h1 {
  margin-left: 9.7%;
  font-size: 60px;
  margin-bottom: 10vh;
  width: 100%;
}
.office {
  font-size: 15px;
  color: #b3b3b3;
  transition: 0.3s;
}
.pos-hr {
  width: 50%;
  height: 1px;
  margin-left: 15%;
  border: none;
  background-color: #7e7e7e;
}
.positions:hover {
  background-color: #b3b3b32a;
  text-shadow: 0px 0px 5px white;
}
.positions:hover .office {
  text-shadow: 0px 0px 0px black;
  transform: translateX(3px);
}
.PD {
  position: absolute;
  top: 380vh;
  background-color: rgb(0, 0, 0);
  height: 100vh;
  width: 100%;
  display: none;
  z-index: 4;
}
.job-pd-title {
  font-size: 40px;
  margin: 11% 0 1% 12.5%;
  display: flex;
  gap: 45%;
}
.jobline {
  width: 100px;
  height: 2px;
  background-color: rgb(178, 176, 176);
  margin-top: 30px;
  cursor: pointer;
}
.job-loc {
  font-size: 15px;
  font-family: graphik;
  color: #848484;
  margin-left: 12.5%;
  line-height: 23px;
}
.job-pd-desc {
  margin-top: 3%;
  width: 54%;
}
.job-req {
  margin: 3.5% 0 4.3% 13%;
  font-size: 27px;
  font-weight: 600;
  width: 35%;
}
.apply {
  margin-left: 12.5%;
  font-family: graphik;
  color: #848484;
}
.apply_btn {
  margin-left: 13%;
  margin-bottom: 10%;
  position: relative;
  top: -30px;
  cursor: pointer;
}
.job-description {
  display: none;
}
.applying-form {
  margin-top: 7%;
  display: none;
}
.fname ,.lname{
  background-color: transparent;
  border: none;
  height: 50px;
  color: white;
  font-size: 16px;
  font-family: graphik;
  margin: 30px;
  width: 40%;
  border-bottom: 1px solid #fff;
}
.resume-here{
  background-color: transparent;
  border: none;
  height: 50px;
  color: white;
  font-size: 12px;
  font-family: graphik;
  margin: 30px;
  width: 40%;
  border-bottom: 1px solid #fff;
  font-style: oblique;
  position: relative;
  left: 25px;
}
.phone{
    background-color: transparent;
  border: none;
  height: 50px;
  color: white;
  font-size: 16px;
  font-family: graphik;
  width: 35%;
  border-bottom: 1px solid #848484;
  position: relative;
  top: 1.4px;
}
.tele{
  background-color: transparent;
  border: none;
  height: 50px;
  color: white;
  font-family: graphik;
  border-bottom: 1px solid #848484;
}
.submit_btn{
  border: 2px solid gold;
  width: 120px;
  height: 30px;
  border-radius: 50px;
  background-color: gold;
  margin-top: 30px;
}
.sub_btn-content{
  color: black;
  font-family: graphik;
  font-size: 15px;
}