.contact-hero {
  width: 100%;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contact-section {
  width: 60%;
  margin-left: 20%;
  margin-top: 130px;
}
.contact-hero-h1 {
  font-size: 78px;
  margin: 20px;
}
.contact-hero-para {
  width: 60%;
  margin-left: 20%;
  line-height: 23px;
  color: #848488;
  font-family: graphik;
  font-size: 15px;
}
.address {
  text-align: center;
  margin-top: 195px;
  width: 100%;
  height: 50vh;
}
.address-h1 {
  font-size: 25px;
  margin: 20px;
}
.address-p {
  color: #848488;
  font-family: graphik;
  line-height: 25px;
  font-size: 15px;
}
.address-loc {
  font-size: 27px;
  font-family: graphik;
  color: #848488;
  margin-top: 65px;
}
.sub_btn {
  display: flex;
  justify-content: center;
  margin-top: 60px;
  width: 230px;
  height: 30px;
  border: 2px solid #686868;
  border-radius: 20px;
  position: relative;
  left: 33%;
  transition: 0.5s;
}
.sub_btn:hover {
  background-color: rgb(232, 202, 33);
  color: black;
  font-weight: bold;
  border: 2px solid rgb(232, 202, 33);
}
.sub_btn:hover .exp_btn-content {
  color: black;
  font-weight: bold;
}
.name {
  background-color: transparent;
  border: none;
  height: 50px;
  color: #848488;
  font-size: 16px;
  font-family: graphik;
  margin-top: 30px;
}
.border-white {
  border-bottom: 2px solid white;
}
.border-grey {
  border-bottom: 2px solid #686868;
}
#form {
  width: 50%;
  margin-left: 25%;
  margin-top: 35px;
  text-align: center;
}
.contact-form-h1 {
  font-size: 40px;
  text-align: center;
  font-weight: bolder;
}
.width-40 {
  width: 45%;
}
.width-35 {
  width: 25%;
}
.lastname {
  margin-left: 10%;
}
.yourmsg {
  width: 100%;
}
.email {
  margin-right: 10%;
}
.service {
  width: 35%;
}
.tele{
    position: relative;
    top: 1px;
    width: 20%;
}
@media screen and (max-width:1250px){
  .contact-hero-h1 {
    font-size: 65px;
  }
  .contact-hero-para {
    width: 80%;
    margin-left: 10%;
  }
}
@media screen and (max-width:1000px){
  .contact-section {
    width: 70%;
    margin-left: 15%;
  }
  .contact-hero-h1 {
    font-size: 52px;
  }
  .contact-hero-para {
    width: 80%;
    margin-left: 10%;
  }
  .contact-form-h1 {
    font-size: 30px;
  }
}
@media screen and (max-width:700px){
  .contact-section {
    width: 90%;
    margin-left: 5%;
  }
  .contact-hero-h1 {
    font-size: 45px;
  }
  .contact-hero-para {
    width: 90%;
    margin-left: 5%;
    line-height: 18px;
    font-size: 12px;
  }
  .address-h1 {
    font-size: 22px;
  }
  .address-p {
    color: #848488;
    font-family: graphik;
    line-height: 22px;
    font-size: 12px;
  }
  .address-loc {
    font-size: 20px;
  }
  .sub_btn {
    position: relative;
    left: 20%;
    width: 60%;
  }
  #form {
    width: 90%;
    margin-left: 5%;
  }
  .contact-form-h1 {
    font-size: 25px;
  }
  .width-40 {
    width: 100%;
  }
  .width-35 {
    width: 80%;
  }
  .lastname {
    margin-left: 0%;
  }
  .yourmsg {
    width: 100%;
  }
  .email {
    margin-right: 0%;
  }
  .service {
    width: 100%;
  }
}