#hero_services {
  font-size: clamp(50px, 57px, 60px);
  font-weight: 900;
  height: 50vh;
  width: 100%;
  font-family: graphikthin;
}
.services_hero-text {
  width: 90%;
  margin-left: 9.7%;
  margin-top: 25%;
  display: flex;
  color: #dedede;
}
.words {
  display: block;
  padding-left: 10px;
  animation: spin-words 4s infinite;
  letter-spacing: 0.003em;
  animation-delay: 2s;
}
.rolling-words {
  overflow: hidden;
  height: 75px;
  position: relative;
  letter-spacing: 0.003em;
  top: 0px;
}
.wedo {
  width: 85%;
  margin-left: 10.5%;
  margin-top: 10%;
  display: flex;
}
.wedo-h1 {
  font-size: 75px;
}
.wedo-para {
  font-size: 15px;
  font-family: graphik;
  color: #848488;
  width: 70%;
  line-height: 23px;
  margin: 30px 0px 60px 0px;
}
.service-container-h1 {
  font-size: 70px;
  width: 60%;
  margin: 5% 0 3% 9%;
}
.accordian {
  position: relative;
  top: 1%;
  left: -2.8%;
  margin-bottom: 15%;
}
.accordian-h1 {
  font-size: 37px;
  color: #d6d6d6;
  margin-bottom: 43px;
}
.accordian-para {
  color: #848484;
  font-family: graphik;
  font-size: 14px;
  line-height: 23px;
  width: 82%;
}
.ser-h1 {
  font-size: 78px;
  margin-left: 9.7%;
}
.ser-imgs {
  display: flex;
  justify-content: start;
  gap: 6em;
  width: 110%;
  margin-top: 140px;
  overflow: hidden;
  margin-left: 13%;
}
.ser-mini {
  width: 100%;
  overflow: hidden;
}
.cns,
.ue,
.pd,
.dm {
  width: 100%;
  height: 70vh;
}
.cns {
  background-image: url("./images/unsplash_9Trha5ptc-c.svg");
  background-size: 100%;
  background-repeat: no-repeat;
}
.ue {
  background-image: url("./images/unsplash_QRWAdBCqysc.svg");
  background-size: 100%;
  background-repeat: no-repeat;
}
.pd {
  background-image: url("./images/unsplash_Im7lZjxeLhg.svg");
  background-size: 100%;
  background-repeat: no-repeat;
}
.dm {
  background-image: url("./images/unsplash_w33-zg-dNL4.svg    ");
  background-size: 100%;
  background-repeat: no-repeat;
}
.ser-txt {
  font-size: 27px;
  margin-top: 65vh;
  color: grey;
}
.model-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 7em;
}
.m-m-container {
  display: flex;
  align-items: center;
  background-color: #111111;
  width: 50%;
  margin-left: 25.5%;
  border-radius: 20px;
  height: 36vh;
  justify-content: space-evenly;
}
.m-desc {
  width: 42%;
  color: #545454;
  font-family: graphik;
}
.m-title {
  font-size: 37px;
  width: 32%;
}
.m-numbers {
  font-size: 15px;
  color: #848484;
  font-family: graphik;
  margin-bottom: 15px;
}
.skip-container {
  display: flex;
  justify-content: center;
}
.e-model {
  width: 40%;
  margin-left: 30.5%;
  text-align: center;
  margin-top: 21%;
  margin-bottom: 5%;
}
.em-h1 {
  font-size: 78px;
  line-height: 85px;
  width: 110%;
  margin-left: -5%;
}
.e-para {
  width: 88%;
  margin-left: 6%;
  font-family: graphik;
  font-size: 15px;
  line-height: 23px;
  margin-top: 20px;
  color: #848484;
}
@keyframes spin-words {
  33% {
    transform: translateY(-100%);
  }
  70% {
    transform: translateY(-205%);
  }
  100% {
    transform: translateY(-310%);
  }
}
@media screen and (max-width: 1440px) {
  #hero_services {
    font-size: 40px;
  }
  .rolling-words {
    height: 50px;
  }
  .em-h1 {
    font-size: 63px;
    line-height: 70px;
    width: 120%;
    margin-left: -10%;
  }
  .m-m-container {
    width: 70%;
    margin-left: 15%;
  }
}
@media screen and (max-width: 1024px) {
  #hero_services {
    font-size: 30px;
  }
  .services_hero-text {
    margin-left: 5%;
  }
  .rolling-words {
    height: 40px;
  }
  .wedo {
    width: 90%;
    margin-left: 5%;
  }
  .wedo-h1 {
    font-size: 55px;
  }
  .wedo-para {
    font-size: 14px;
    line-height: 20px;
  }
  .accordian-h1 {
    font-size: 30px;
  }
  .accordian-para {
    font-size: 14px;
    line-height: 20px;
    width: 90%;
  }
  .ser-h1 {
    font-size: 58px;
    margin-left: 5%;
  }
  .ser-imgs {
    gap: 3em;
    width: 130%;
    margin-left: 8%;
  }
  .ser-txt {
    font-size: 20px;
  }
  .e-model {
    width: 60%;
    margin-left: 20%;
  }
  .e-para {
    font-size: 13px;
    line-height: 20px;
  }
  .m-m-container {
    width: 80%;
    margin-left: 10%;
  }
  .m-title {
    font-size: 30px;
    width: 32%;
  }
  .m-numbers {
    font-size: 12px;
  }
  .m-desc {
    font-size: 13px;
  }
}
@media screen and (max-width: 768px) {
  #hero_services {
    font-size: 25px;
  }
  .services_hero-text {
    margin-left: 5%;
    width: 95%;
  }
  .rolling-words {
    height: 35px;
  }
  .wedo-h1 {
    font-size: 45px;
  }
  .wedo-para {
    font-size: 12px;
    line-height: 18px;
  }
  .accordian {
    left: 0;
  }
  .accordian-h1 {
    font-size: 25px;
  }
  .accordian-para {
    font-size: 12px;
    line-height: 18px;
  }
  .ser-h1 {
    font-size: 48px;
  }
  .ser-imgs {
    gap: 2em;
    width: 140%;
  }
  .ser-txt {
    font-size: 18px;
  }
  .e-model {
    width: 90%;
    margin-left: 5%;
    margin-top: 11%;
  }
  .em-h1 {
    font-size: 38px;
    line-height: 45px;
    width: 100%;
    margin-left: 0%;
  }
  .e-para {
    font-size: 12px;
    line-height: 18px;
  }
  .m-m-container {
    width: 90%;
    margin-left: 5%;
  }
  .m-title {
    font-size: 25px;
  }
  .m-desc {
    width: 60%;
  }
}
@media screen and (max-width: 600px) {
  .services_hero-text {
    flex-direction: column;
  }
  .words {
    padding-left: 0;
  }
  .wedo {
    width: 90%;
    margin-left: 5%;
    flex-direction: column;
    text-align: center;
  }
  .wedo-para {
    width: 90%;
    margin-left: 5%;
  }
  .accordian {
    margin-bottom: 5%;
  }
  .accordian-para {
    margin-left: 5%;
    width: 90%;
  }
  .accordian-h1 {
    margin-bottom: 10px;
    margin-top: 40px;
  }
  .ser-imgs {
    flex-direction: column;
  }
  .ser-h1 {
    font-size: 30px;
    margin-top: 10%;
  }
  .ser-imgs {
    gap: 4em;
    width: 90%;
    margin-left: 5%;
    margin-top: 40px;
  }
  .m-m-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: auto;
    padding: 5% 0;
  }
  .m-desc {
    width: 90%;
    font-size: 12px;
  }
  .m-title {
    width: 80%;
    font-size: 22px;
  }
}
