#socials{
    display: flex;
    flex-direction: row;
    gap: 1.3em;
    width: 25%;
    position: relative;
    bottom: 20px;
    left: 8%;
}
#footer{
    position: relative;
    top: 50px;
    max-width: 100%;
    background-color: black;
}
#ftrcnt{
    font-family: graphikthin;
    color: rgba(255, 255, 255, 0.612);
    font-weight: 500;
    font-size: 27.5px;
    white-space: nowrap;
    display: flex;
    justify-content: center;
    gap: 10px;
}
#ftrh1{
    font-size: clamp(20px,238px,300px);
    color: rgb(0, 0, 0);
    text-align: center;
    margin-bottom: 120px;
    font-weight: bold;
    transition: 0.5s;
    white-space: nowrap;
    font-family: Graphik;
    letter-spacing: -0.045em;
    font-weight: 100;
    padding-bottom: 50px;
}
.copyright{
    color: #828282;
    font-size: 13px;
    word-spacing: 0.03em;
    font-family: graphik;
    margin: 15px 0px 0px 3px;
}
#ftrh1:hover{
    text-shadow: 2px 2px 90px rgb(118,116,116);
}
#lower-footer{
    display: flex;
    gap: 3.8em;
    width: 62%;
    font-weight: 700;
    font-size: 15px;
    letter-spacing: 0.01em;
}
#contact{
    line-height: 44px;
    font-size: 15px;
    position: relative;
    top: 4px;
    left: 5.4%;
    letter-spacing: 0.05em;
}
.arrow{
    width: 90px;
    position: relative;
    top: 15%;
}
.icon{
    width: 22px;
}
.column{
    line-height: 44px;
    letter-spacing: 0.05em;
    white-space: nowrap;
}
.footerlink{
    display: flex;
    text-decoration: none;
}
.link{
    text-decoration: none;
}
.lower-footer{
    display: flex;
    width: 80%;
    margin-left: 9.7%;
    justify-content: space-between;
    margin-bottom: 94px;
}
.codelinear{
    width: 107px;
    position: relative;
}
@media screen and (max-width:1300px){
    #lower-footer{
        gap: 1.5em;
    }
    #socials{
        width: 20%;
        left: 5%;
    }
}
@media screen and (max-width:1000px){
    #ftrcnt{
        font-size: 20px;   
    }
    .arrow{
        width: 80px;
    }
    #ftrh1{
        font-size:100px;
    }
    .lower-footer{ 
        margin-left: 0%;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        width: 100%;
    }
    .codelinear{
        left: 0;
    }
    #socials{
        left: 0;
    }
    #lower-footer{
        justify-content: space-around;
        width: 100%;
    }
}
@media screen and (max-width:700px){
    .column{
        display: flex;
        width: 100%;
        justify-content: space-evenly;
        margin-left:0px ;
    }
    #ftrh1{
        font-size:70px;
    }
    #socials{
        bottom: 15px;
        width: 100%;
        justify-content: center;
        gap: 1em;
    }
    #contact{
        left: 0px;
    }
    .arrow{
        width: 50px;
    }
    #ftrcnt{
        font-size: 12px;
    }
    #lower-footer{
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: space-around;
    }
}