#hero {
  width: 100%;
  height: 100vh;
  background-color: black;
}
.bgv {
  width: 140%;
  position: absolute;
  top: -30%;
  left: 12%;
  box-sizing: content-box;
}
.bgi {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
#content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  position: absolute;
  max-width: 84%;
  height: auto;
  left: 9.8%;
  top: 38%;
}
#header {
  font-weight: bolder;
  font-size: 59px;
  line-height: 70px;
  word-spacing: 0.01em;
  width: 70%;
}
#aboutbtn {
  margin-top: 40px;
  width: 122px;
  font-size: 15.4px;
  height: 32px;
  text-align: center;
  border: 1px solid #707070;
  border-radius: 20px;
  transition: 0.5s;
  position: relative;
  left: -1px;
}
.aboutbtn_content {
  font-family: graphik;
  position: relative;
  top: 4px;
  color: #747474;
}
#aboutbtn:hover {
  background-color: #ffcd05;
  color: black;
  font-weight: bold;
  border: 1px solid #ffcd05;
}
#aboutbtn:hover .aboutbtn_content {
  color: black;
  font-weight: bold;
}
#about {
  position: relative;
  width: 100%;
  height: auto;
}
#rectangle {
  position: absolute;
  width: 3100px;
  height: 832px;
  right: 1%;
  top: 375px;
  background: #000000;
  transform: rotate(-33.2deg);
  z-index: 2;
  border-radius: 0px 100% 0px 0px;
}
#abtcnt {
  z-index: 3;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  position: absolute;
  width: 70%;
  height: 60vh;
  left: 9.7%;
  top: 210px;
}
#abth1 {
  position: relative;
  margin: 0px;
  color: white;
  width: 100%;
  font-weight: 100;
  font-size: 76px;
  line-height: 80px;
  text-transform: capitalize;
  font-weight: bolder;
  word-spacing: 0.01em;
  letter-spacing: 0.01em;
}
#abtdmycnt {
  color: #898989;
  align-self: stretch;
  font-size: 15px;
  margin: 0;
  width: 65%;
  font-family: graphik;
  font-weight: 100;
  margin-top: 29px;
  line-height: 23px;
}
.allworks {
  text-decoration: none;
  position: relative;
  top: 49px;
  width: 122px;
  height: 32px;
  border: 1px solid #898989;
  font-family: graphik;
  border-radius: 20px;
  text-align: center;
  transition: 0.5s;
  font-size: 15px;
}
.workbtn_content {
  color: #898989;
  position: relative;
  top: 4px;
}
.allworks:hover {
  background-color: #ffcd05;
  color: black;
  font-weight: bold;
  border: 1px solid #ffcd05;
}
.allworks:hover .workbtn_content {
  color: black;
  font-weight: bold;
}
.bg-images {
  display: flex;
  justify-content: center;
  gap: 6.5em;
}
.bg-img-container-lower {
  position: relative;
  top: 340px;
  margin-left: 15px;
}
.bg-img-container,
.bg-img-container-lower {
  width: 37.3%;
  display: flex;
  flex-direction: column;
  gap: 10em;
  margin-top: 300px;
}
#imgs {
  background-image: url("./images/image\ 5.svg");
  background-size: 305%;
  width: 100%;
  height: 888px;
  background-repeat: no-repeat;
  background-position: -640px -100px;
  transition: 0.3s;
}
#imgs:hover {
  background-size: 100%;
  background-position: 0px 550px;
}
#imgs1 {
  display: flex;
  color: white;
  font-size: 18px;
  margin-top: 25px;
}
#imgs1p {
  position: relative;
  top: 7px;
  left: 15px;
  font-size: 15px;
  color: #585858;
  font-family: graphik;
}
#img2 {
  background-image: url("./images/image 11.svg");
  background-size: 168%;
  width: 100%;
  height: 973px;
  background-repeat: no-repeat;
  background-position: -235px 105px;
  transition: 0.5s;
}
#img2:hover {
  background-size: 100%;
  background-position: 0px 540px;
}
#imgs2 {
  display: flex;
  color: white;
  font-size: 18px;
  margin-top: 28px;
}
#imgs2p {
  position: relative;
  top: 8px;
  left: 15px;
  font-size: 15px;
  color: #a0a0a0;
}
#img3 {
  background-image: url("./images/image\ 4.svg");
  background-size: 180%;
  width: 100%;
  height: 745px;
  background-repeat: no-repeat;
  background-position: -200px -30px;
  transition: 0.5s;
}
#img3:hover {
  background-size: 100%;
  background-position: 0px 0px;
}
#imgs3 {
  display: flex;
  color: white;
  font-size: 18px;
  margin: 5px 0px 27px 0px;
}
#imgs3p {
  position: relative;
  top: 8px;
  left: 15px;
  font-size: 15px;
  color: #a0a0a0;
}
#imgs4 {
  color: white;
  display: flex;
  font-size: 18px;
  margin-bottom: 27px;
}
#imgs4p {
  position: relative;
  top: 8px;
  left: 15px;
  font-size: 15px;
  color: #a0a0a0;
}
#img4 {
  background-image: url("./images/image\ 3.svg");
  background-size: 130%;
  width: 100%;
  height: 400px;
  background-repeat: no-repeat;
  background-position: -70px -40px;
  transition: 0.5s;
}
.bg-mini-4 {
  position: relative;
  top: -78px;
}
#img4:hover {
  background-size: 79%;
  background-position: 50px -70px;
}
#service {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
}
.service_main {
  width: 80%;
  margin: 11% 0px;
  display: flex;
  justify-content: space-between;
}
#srvcnt {
  margin: 0;
  font-weight: bolder;
  font-size: 78px;
}
#srvp {
  position: relative;
  margin: 5% 0px;
  font-weight: 300;
  font-family: graphik;
  font-size: 15.5px;
  letter-spacing: -0.02em;
  color: #848484;
}
#svcbtn {
  position: relative;
  border: 1px solid #8c8c8c;
  width: 120px;
  height: 32px;
  margin-top: 25px;
  border-radius: 20px;
  transition: 0.5s;
  position: relative;
  left: -2px;
}
.svcbtn_content {
  color: #828282;
  position: relative;
  top: 4px;
  text-align: center;
  height: 30px;
  font-family: graphik;
  font-size: 15px;
}
#svcbtn:hover {
  background-color: #ffcd05;
  color: black;
  border: 1px solid #ffcd05;
}
#svcbtn:hover .svcbtn_content {
  color: black;
  font-weight: bold;
}
#buttons {
  font-size: 39.5px;
  width: 31%;
  height: 390px;
  margin-top: 27px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  left: -8px;
}
.btnlinks {
  text-decoration: none;
  color: #807e7e;
  font-weight: 700;
  transition: 0.3s;
  line-height: 1.1em;
}

.btnlinks:hover {
  padding-left: 40px;
  color: white;
  text-shadow: 0px 0px 20px white;
}
#testimonials {
  width: 100%;
  height: 120vh;
  background-color: black;
  color: white;
}
#container-2_header {
  font-size: 75px;
  margin: 0;
  text-align: center;
  font-weight: 900;
  line-height: 85px;
}
#nums {
  color: rgb(182, 182, 182);
  position: relative;
  top: 42px;
  left: 44%;
  display: inline;
  font-size: 17px;
}
#left,
#right {
  width: 44px;
  height: 44px;
  border-radius: 360px;
}
#arrow {
  display: flex;
  width: 110px;
  justify-content: space-between;
  position: relative;
  left: 49%;
  top: 10px;
}
#container-2 {
  width: 55%;
  position: relative;
  margin-left: 23%;
  margin-top: 122px;
}
#container {
  position: relative;
  margin-right: 0;
  width: 88%;
  height: 100px;
  margin-top: 163px;
  left: 22%;
}
#container-h3 {
  width: 62%;
  font-size: 15.5px;
  position: relative;
  font-weight: 600;
  line-height: 23px;
  word-spacing: 0.04em;
  letter-spacing: 0.01em;
}
#pm {
  line-height: 0px;
  margin-top: 10px;
  font-size: 15px;
  color: #a0a0a0;
  position: relative;
  font-weight: 900;
  word-spacing: 0.03em;
  letter-spacing: 0.04em;
}
#container_h2 {
  margin: 19px 1px 10px 0;
  font-size: 27px;
  position: relative;
  top: 0px;
  font-weight: 700;
}
#clients {
  position: relative;
  top: 270px;
  left: 21.5%;
  width: 58%;
}
#blog {
  width: 100%;
  height: 100vh;
  background-color: black;
  color: rgb(255, 255, 255);
}
#blog_heading {
  position: relative;
  margin-left: 19.5%;
  font-weight: 900;
  color: white;
  font-size: 76px;
  width: 50%;
  line-height: 80px;
  margin-top: 130px;
}
#blog-icons {
  width: 63%;
  padding-left: 19%;
  padding-top: 85px;
  top: 110px;
  display: flex;
  justify-content: space-between;
  text-align: center;
}
.blog-icons_img {
  width: 285px;
  height: 160px;
  border-radius: 10px;
}
.blog-icons_div {
  max-width: 30%;
  height: 310px;
}
.blog_para {
  font-size: clamp(10px, 15px, 20px);
  color: #a0a0a0;
  line-height: 23px;
  text-align: center;
  display: flex;
  font-family: graphik;
  width: 90%;
  padding-left: 5%;
}
.blog-icon_div-h2 {
  color: rgb(210, 210, 210);
  font-size: 27px;
  font-weight: 900;
  margin-top: 20px;
  margin-bottom: 12px;
}
.explore_btn {
  display: flex;
  justify-content: center;
  margin-top: 60px;
  width: 150px;
  height: 30px;
  border: 2px solid #686868;
  border-radius: 20px;
  position: relative;
  left: 45%;
  transition: 0.5s;
}
.exp_btn-content {
  position: relative;
  top: 3px;
  color: #797979;
  font-family: graphik;
}
.explore_btn:hover {
  background-color: rgb(232, 202, 33);
  color: black;
  font-weight: bold;
  border: 2px solid rgb(232, 202, 33);
}
.explore_btn:hover .exp_btn-content {
  color: black;
  font-weight: bold;
}
@media screen and (min-width: 2100px) {
  #imgs {
    height: 1600px;
    background-position: -1140px -180px;
  }
  #img2 {
    height: 1673px;
    background-position: -435px 355px;
  }
  #imgs:hover {
    background-size: 100%;
    background-position: 0px 1050px;
  }
  #img2:hover {
    background-size: 100%;
    background-position: 0px 1040px;
  }
  #img3 {
    height: 1245px;
    background-repeat: no-repeat;
    background-position: -330px -30px;
    transition: 0.5s;
  }
  #img4 {
    height: 910px;
    background-repeat: no-repeat;
    background-position: -70px -40px;
    transition: 0.5s;
  }
  #rectangle {
    top: 300px;
  }
}
@media screen and (max-width: 1300px) {
  #blog-icons {
    width: 85%;
    padding-left: 10%;
  }
  #blog_heading {
    margin-left: 10%;
    width: 70%;
  }
  #container-2 {
    width: 70%;
    margin-left: 20%;
  }
  #container-2_header {
    font-size: 70px;
  }
  #imgs {
    height: 700px;
  }
  #img2 {
    height: 780px;
  }
  #imgs1,
  #imgs2,
  #imgs3,
  #imgs4 {
    font-size: 15px;
  }
  #imgs1p,
  #imgs2p,
  #imgs3p,
  #imgs4p {
    font-size: 12px;
  }
  .bg-img-container-lower {
    margin-top: 150px;
  }
}
@media screen and (max-width: 1000px) {
  #blog-icons {
    width: 95%;
    padding-left: 2.5%;
  }
  #blog_heading {
    margin-left: 2.5%;
    width: 90%;
  }
  .blog-icons_img {
    width: 245px;
    border-radius: 20px;
  }
  #container-2_header {
    font-size: 50px;
    line-height: 58px;
  }
  #buttons {
    height: 450px;
  }
  #srvcnt {
    font-size: 60px;
  }
  #imgs {
    height: 500px;
  }
  #img2 {
    height: 580px;
  }
  #imgs1,
  #imgs2,
  #imgs3,
  #imgs4 {
    font-size: 12px;
  }
  #imgs1p,
  #imgs2p,
  #imgs3p,
  #imgs4p {
    font-size: 8px;
  }
  #abth1 {
    font-size: 50px;
    line-height: 58px;
  }
  .bg-img-container-lower {
    top: 100px;
  }
  .bg-img-container-lower,
  .bg-img-container {
    margin-top: 270px;
  }
  #imgs {
    background-position: -460px 15px;
  }
  #img2 {
    background-position: -135px 150px;
  }
}
@media screen and (max-width: 800px) {
  #header {
    font-size: 40px;
    line-height: 40px;
  }
  #content {
    left: 100px;
  }
  .blog-icons_img {
    width: 165px;
    border-radius: 20px;
  }
  #blog_heading {
    font-size: 60px;
  }
  .blog-icon_div-h2 {
    font-size: 20px;
  }
  .blog_para {
    font-size: 12px;
    line-height: 18px;
  }
  #nums {
    left: 40%;
  }
  #srvcnt {
    font-size: 50px;
  }
  #srvp {
    font-size: 13px;
  }
  #buttons {
    font-size: 30px;
    left: 0;
  }
  #abth1 {
    font-size: 40px;
    line-height: 38px;
  }
  .bg-images {
    flex-direction: column;
  }
  .bg-img-container,
  .bg-img-container-lower {
    width: 80%;
    margin-left: 10%;
    margin-top: 100px;
  }
  .bg-img-container-lower {
    margin-left: 10%;
    top: 50px;
  }
  #img2 {
    height: 880px;
  }
  #rectangle {
    height: 400px;
    top: 800px;
    right: -15%;
    border-radius: 0px;
  }
}
@media screen and (max-width: 600px) {
  #header {
    font-size: 30px;
    line-height: 35px;
  }
  #content {
    left: 70px;
  }
  #blog-icons {
    flex-direction: column;
    align-items: center;
    gap: 3em;
    width: 80%;
  }
  .blog-icons_div {
    max-width: 90%;
  }
  .blog-icons_img {
    width: 265px;
    border-radius: 20px;
  }
  #blog_heading {
    font-size: 40px;
    line-height: 50px;
  }
  .blog-icon_div-h2 {
    font-size: 25px;
  }
  .blog_para {
    font-size: 15px;
    line-height: 23px;
  }
  #blog {
    height: auto;
  }
  #abth1 {
    font-size: 30px;
    line-height: 35px;
  }
  .explore_btn {
    left: 20%;
  }
  #container-2_header {
    font-size: 30px;
    line-height: 35px;
  }
  #container-2 {
    width: 90%;
    margin-left: 5%;
  }
  #container-h3 {
    width: 100%;
  }
  #nums {
    left: 35%;
  }
  #container {
    width: 90%;
    left: 5%;
  }
  .service_main {
    flex-direction: column;
    margin: 5% 0%;
    justify-content: start;
  }
  #buttons {
    font-size: 30px;
    left: 0;
    width: 90%;
    height: 350px;
  }
  #abtdmycnt {
    font-size: 12px;
    line-height: 16px;
    margin-top: 10px;
    width: 90%;
  }
  .allworks {
    top: 10px;
  }
  #rectangle {
    display: none;
  }
  .bg-img-container-lower {
    margin-left: 10%;
    top: 250px;
  }
  #img2 {
    height: 480px;
  }
}
